<template>
  <div class="params">
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <el-card>
      <el-alert title="" type="warning"></el-alert>
      <el-row>
        <el-col :span="12">
          <span></span>
          <!--            -->
          <el-cascader v-model="selectKeys" :options="categoriesData"
            :props="categoriesProps" @change="categoriesChange" clearable>
          </el-cascader>
        </el-col>
      </el-row>
      <!--        -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="dynamic">
          <el-row>
            <el-col :span="4">
              <el-button type="primary" size="mini" class="add-attr"
                :disabled="isBtnDisplay" @click="openAdd">
                {{ "" + paramsOrAttr }}
              </el-button>
            </el-col>
            <el-table :data="dynamicData" border stripe>
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag :key="i" v-for="(tag, i) in scope.row.attr_vals"
                    closable :disable-transitions="false"
                    @close="handleClose(i, scope.row)">
                    {{ tag }}
                  </el-tag>
                  <el-input v-if="scope.row.inputVisible" class="inputTag"
                    v-model="scope.row.inputValue" ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)">
                  </el-input>
                  <el-button v-else size="small" @click="showInput(scope.row)">+
                    New Tag
                  </el-button>
                </template>
              </el-table-column>
              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column prop="attr_name" label="">
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button icon="el-icon-edit" type="primary"
                    @click="openEdit(scope.row)" size="mini">
                  </el-button>
                  <el-button icon="el-icon-delete" type="danger"
                    @click="deleteByAttrId(scope.row)" size="mini">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="static">
          <el-row>
            <el-col :span="4">
              <el-button type="primary" size="mini" class="add-attr"
                :disabled="isBtnDisplay" @click="openAdd">
                {{ "" + paramsOrAttr }}
              </el-button>
            </el-col>
            <el-table :data="dynamicData" border stripe>
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag :key="i" v-for="(tag, i) in scope.row.attr_vals"
                    closable :disable-transitions="false"
                    @close="handleClose(i, scope.row)">
                    {{ tag }}
                  </el-tag>
                  <el-input v-if="scope.row.inputVisible" class="inputTag"
                    v-model="scope.row.inputValue" ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)">
                  </el-input>
                  <el-button v-else size="small" @click="showInput(scope.row)">+
                    New Tag
                  </el-button>
                </template>
              </el-table-column>
              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column prop="attr_name" label=""></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button icon="el-icon-edit" type="primary"
                    @click="openEdit(scope.row)" size="mini">
                  </el-button>
                  <el-button icon="el-icon-delete" type="danger"
                    @click="deleteByAttrId(scope.row)" size="mini">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-row>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--      -->
    <el-dialog :title="'' + paramsOrAttr" :visible.sync="addParamsVisible"
      @close="addParamsClose" width="50%">
      <el-form :model="addForm" :rules="addrules" ref="addForm"
        label-width="80px">
        <el-form-item :label="paramsOrAttr" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addParamsVisible = false"> </el-button>
        <el-button type="primary" @click="addFormSubmit"> </el-button>
      </span>
    </el-dialog>
    <!--      -->
    <el-dialog :title="'' + paramsOrAttr" :visible.sync="editParamsVisible"
      @close="editParamsClose" width="50%">
      <el-form :model="editForm" :rules="editrules" ref="editForm"
        label-width="80px">
        <el-form-item :label="paramsOrAttr" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editParamsVisible = false"> </el-button>
        <el-button type="primary" @click="editFormSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getCategories } from '../../../network/goodsCom/Categories'
import {
  getAttributesById,
  getParamsByAttrId,
  addParams,
  editParams,
  deleteByAttrId
} from '../../../network/goodsCom/Params'

export default {
  name: 'Params',
  data () {
    return {
      // id
      selectKeys: [],
      // 
      categoriesData: [],
      // 
      categoriesProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      activeName: 'dynamic',
      // 
      dynamicData: [],
      // 
      addParamsVisible: false,
      addForm: {
        name: ''
      },
      addrules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editParamsVisible: false,
      editForm: {
        attrId: '',
        name: ''
      },
      editrules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getCategories()
  },
  computed: {
    paramsOrAttr () {
      if (this.activeName === 'dynamic') {
        return ''
      } else {
        return ''
      }
    },
    isBtnDisplay () {
      if (this.selectKeys.length !== 3) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    // 
    handleClick () {
      if (this.isBtnDisplay === true) return false
      const id = this.selectKeys[this.selectKeys.length - 1]
      const sel = this.activeName === 'dynamic' ? 'many' : 'only'
      this.getAttributesById(id, sel)
    },
    // 
    categoriesChange () {
      const id = this.selectKeys[this.selectKeys.length - 1]
      const sel = this.activeName === 'dynamic' ? 'many' : 'only'
      this.getAttributesById(id, sel)
    },
    // tag
    handleClose (i, row) {
      this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        row.attr_vals.splice(i, 1)
        this.funcAddChangeTag(row)
      }).catch(err => {
        console.log(err)
      })
    },
    // taginput
    showInput (row) {
      row.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // input
    handleInputConfirm (row) {
      // 
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // return 
      row.attr_vals.push(row.inputValue.trim())
      this.funcAddChangeTag(row)
    },
    // tag,
    funcAddChangeTag (row) {
      // 
      const id = this.selectKeys[this.selectKeys.length - 1]
      const attrVals = row.attr_vals.join(' ')
      this.editParams(id, row.attr_id, row.attr_name, row.attr_sel, attrVals)
      row.inputVisible = false
      row.inputValue = ''
    },
    // ""
    openAdd () {
      this.addParamsVisible = true
    },
    // ""
    addParamsClose () {
      this.addForm.name = ''
      this.addParamsVisible = false
    },
    // 
    addFormSubmit () {
      this.$refs.addForm.validate(validate => {
        if (!validate) return false
        const id = this.selectKeys[this.selectKeys.length - 1]
        const name = this.addForm.name
        const sel = this.activeName === 'dynamic' ? 'many' : 'only'
        this.addParams(id, name, sel)
        this.addParamsVisible = false
      })
    },
    // ""
    openEdit (row) {
      this.editForm.name = row.attr_name
      this.editForm.attrId = row.attr_id
      this.editParamsVisible = true
    },
    // ""
    editParamsClose () {
      this.editForm.name = ''
      this.editForm.attrId = ''
      this.editParamsVisible = false
    },
    // ""
    editFormSubmit () {
      this.$refs.editForm.validate(validate => {
        if (!validate) return false
        const id = this.selectKeys[this.selectKeys.length - 1]
        const attrId = this.editForm.attrId
        const name = this.editForm.name
        const sel = this.activeName === 'dynamic' ? 'many' : 'only'
        this.editParams(id, attrId, name, sel)
        this.editParamsVisible = false
      })
    },
    // ()
    getCategories (type = '', pageNum = '', pageSize = '') {
      getCategories(type, pageNum, pageSize)
        .then(res => {
          if (res.data.meta.status !== 200) {
            return this.$message.error(res.data.meta.msg)
          }
          // 
          this.categoriesData = res.data.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    // id()
    getAttributesById (id, sel) {
      getAttributesById(id, sel)
        .then(res => {
          if (res.data.meta.status !== 200) {
            return this.$message.error(res.data.meta.msg)
          }
          console.log(res)
          // 
          res.data.data.forEach(item => {
            item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
            // 
            item.inputVisible = false
            // 
            item.inputValue = ''
          })
          this.dynamicData = res.data.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    // idattrId()
    getParamsByAttrId (id, attrId, attrSel, attrVals = '') {
      getParamsByAttrId(id, attrId, attrSel, attrVals)
        .then(res => {
          console.log(res)
        })
        .catch(err => {
          console.log(err)
        })
    },
    // ()
    addParams (id, attrName, attrSel, attrVals = '') {
      addParams(id, attrName, attrSel, attrVals)
        .then(res => {
          console.log(res)
          if (res.data.meta.status !== 201) {
            return this.$message.error(res.data.meta.msg)
          }
          this.$message.success(res.data.meta.msg)
          // 
          this.getAttributesById(id, attrSel)
        })
        .catch(err => {
          console.log(err)
        })
    },
    // ()
    editParams (id, attrId, attrName, attrSel, attrVals = '') {
      editParams(id, attrId, attrName, attrSel, attrVals)
        .then(res => {
          console.log(res)
          if (res.data.meta.status !== 200) {
            return this.$message.error(res.data.meta.msg)
          }
          this.$message.success(res.data.meta.msg)
          // 
          if (attrVals === '') {
            this.getAttributesById(id, attrSel)
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // id()
    deleteByAttrId (row) {
      const id = this.selectKeys[this.selectKeys.length - 1]
      const attrId = row.attr_id
      const attrSel = this.activeName === 'dynamic' ? 'many' : 'only'
      // 
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        deleteByAttrId(id, attrId).then(res => {
          if (res.data.meta.status !== 200) return this.$message.error(res.data.meta.msg)
          this.$message.success(res.data.meta.msg)
          this.getAttributesById(id, attrSel)
        }).catch(err => {
          console.log(err)
        })
      }).catch(() => {
        // 
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style scoped>
.el-alert {
  margin-bottom: 15px;
}
.el-cascader {
  width: 70%;
}
.add-attr {
  margin-bottom: 15px;
}
.el-tag {
  margin-right: 15px;
}
.inputTag {
  max-width: 150px;
}
</style>
