<template>
  <div class="Categories">
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary" @click="addCategoryClick"></el-button>
        </el-col>
      </el-row>
      <tree-table :data="categoriesData" :columns="columns"
        :selection-type="false" :expand-type="false" :show-index="true"
        index-text="#" :show-row-hover="false" border>
        <!--        -->
        <template slot="isOk" slot-scope="scope">
          <i v-if="scope.row.cat_deleted === false" class="el-icon-success"
            style="color: lightgreen"></i>
          <i v-if="scope.row.cat_deleted === true" class="el-icon-error"
            style="color: red"></i>
        </template>
        <!--        -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level == 0" size="mini"></el-tag>
          <el-tag v-else-if="scope.row.cat_level == 1" type="success"
            size="mini"></el-tag>
          <el-tag v-else-if="scope.row.cat_level == 2" type="warning"
            size="mini"></el-tag>
        </template>
        <!--        -->
        <template slot="option" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="
              updateCategoriesByIdClick(scope.row.cat_id, scope.row.cat_name)
            "></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini"
            @click="deleteCategoriesByIdClick(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>
      <el-pagination @size-change="categoriesSizeChange"
        @current-change="categoriesCurrentChange" :current-page="pageNum"
        :page-sizes="[1, 2, 5, 10]" :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--      -->
    <el-dialog title="" :visible.sync="addCategoryVisible" width="60%"
      @close="addCategoryClose">
      <el-form :model="addCategoryForm" :rules="addCategoryRules"
        ref="addCategoryForm" label-width="80px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCategoryForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader v-model="selectKeys" :options="categoriesParentData"
            :props="categoriesProps" @change="parentCateChange" clearable>
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addCategoryVisible = false"> </el-button>
        <el-button type="primary" @click="addCategorySubmit"> </el-button>
      </span>
    </el-dialog>
    <!--    -->
    <el-dialog title="" :visible="updateCategoryVisible" width="60%"
      @close="updateCategoryClose">
      <el-form :model="updateCategoryForm" :rules="updateCategoryRules"
        ref="updateCategoryForm" label-width="80px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="updateCategoryForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="updateCategoryVisible = false"> </el-button>
        <el-button type="primary" @click="updateCategorySubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getCategories,
  addCategories,
  deleteCategoriesById,
  updateCategories
} from '../../../network/goodsCom/Categories'

export default {
  name: 'Categories',
  data () {
    return {
      categoriesData: [],
      // tree-table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          prop: 'cat_deleted',
          type: 'template',
          template: 'isOk'
        },
        {
          label: '',
          prop: 'cat_level',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          width: '200px',
          minwidth: '200px',
          type: 'template',
          template: 'option'
        }
      ],
      // categoriesData
      type: 3,
      // 
      pageNum: 1,
      pageSize: 10,
      total: 0,
      // 
      updateCategoryVisible: false,
      updateCategoryForm: {
        cat_name: '',
        cat_id: 0
      },
      // 
      updateCategoryRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      addCategoryVisible: false,
      addCategoryForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      // 
      addCategoryRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      categoriesParentData: [],
      // 
      categoriesProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      // Id
      selectKeys: []
    }
  },
  created () {
    this.getCategories()
  },
  methods: {
    // 
    parentCateChange () {
      console.log(this.selectKeys)
      if (this.selectKeys.length > 0) {
        this.addCategoryForm.cat_pid = this.selectKeys[
          this.selectKeys.length - 1
        ]
        this.addCategoryForm.cat_level = this.selectKeys.length
      } else {
        this.addCategoryForm.cat_pid = 0
        this.addCategoryForm.cat_level = 0
      }
    },
    // ""
    addCategoryClose () {
      this.addCategoryForm.cat_name = ''
      this.selectKeys = ''
      this.addCategoryVisible = false
    },
    // ""
    updateCategoryClose () {
      this.updateCategoryForm.cat_name = ''
      this.updateCategoryVisible = false
    },
    // 
    addCategorySubmit () {
      this.$refs.addCategoryForm.validate(validate => {
        if (!validate) return false
        this.addCategories()
        this.addCategoryVisible = false
      })
    },
    // 
    updateCategorySubmit () {
      this.$refs.updateCategoryForm.validate(validate => {
        if (!validate) return false
        this.updateCategories()
        this.updateCategoryVisible = false
      })
    },
    // 
    categoriesSizeChange (val) {
      this.pageSize = val
      this.getCategories()
    },
    // 
    categoriesCurrentChange (val) {
      this.pageNum = val
      this.getCategories()
    },
    // 
    addCategoryClick () {
      this.addCategoryVisible = true
      this.getCategories(2, '', '')
    },
    // 
    updateCategoriesByIdClick (id, name) {
      this.updateCategoryForm.cat_id = id
      this.updateCategoryForm.cat_name = name
      this.updateCategoryVisible = true
    },
    // 
    deleteCategoriesByIdClick (id) {
      console.log(id)
      this.deleteCategoriesById(id)
    },
    // ()
    getCategories (
      type = this.type,
      pageNum = this.pageNum,
      pageSize = this.pageSize
    ) {
      getCategories(type, pageNum, pageSize)
        .then(res => {
          if (res.data.meta.status !== 200) {
            return this.$message.error(res.data.meta.msg)
          }
          if (type === 2) {
            console.log(res)
            this.categoriesParentData = res.data.data
          } else {
            console.log(res)
            this.categoriesData = res.data.data.result
            this.total = res.data.data.total
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // ()
    addCategories (
      catPid = this.addCategoryForm.cat_pid,
      catName = this.addCategoryForm.cat_name,
      catLevel = this.addCategoryForm.cat_level
    ) {
      addCategories(catPid, catName, catLevel)
        .then(res => {
          console.log(res)
          if (res.data.meta.status !== 201) {
            return this.$message.error(res.data.meta.msg)
          }
          this.$message.success(res.data.meta.msg)
          this.getCategories()
        })
        .catch(err => {
          console.log(err)
        })
    },
    // id()
    deleteCategoriesById (id) {
      deleteCategoriesById(id)
        .then(res => {
          console.log(res)
          // 
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
            .then(() => {
              // 
              if (res.data.meta.status !== 200) {
                return this.$message.error(res.data.meta.msg)
              }
              this.$message.success(res.data.meta.msg)
              this.getCategories()
            })
            .catch(() => {
              // 
              this.$message({
                type: 'info',
                message: ''
              })
            })
        })
        .catch(err => {
          console.log(err)
        })
    },
    // ()
    updateCategories (
      id = this.updateCategoryForm.cat_id,
      name = this.updateCategoryForm.cat_name
    ) {
      updateCategories(id, name)
        .then(res => {
          console.log(res)
          if (res.data.meta.status !== 200) {
            return this.$message.error(res.data.meta.msg)
          }
          this.$message.success(res.data.meta.msg)
          this.getCategories()
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
