<!--  -->
<template>
  <div class="goodsAdd">
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false">
      </el-alert>
      <!--  -->
      <el-steps :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <el-form ref="addGoodFormRef" :rules="addGoodRules" :model="addGoodForm"
        label-position="top" label-width="100px">
        <el-tabs tab-position="left" v-model="activeIndex"
          :before-leave="beforeTabsLeave" @tab-click="tabClick">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addGoodForm.goods_price" type="number">
              </el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addGoodForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addGoodForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="">
              <!--  -->
              <el-cascader v-model="addGoodForm.goods_cat"
                :options="categoriesData" :props="categoriesProps"
                @change="handleChange" clearable>
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData"
              :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="name" v-for="(name, i) in item.attr_vals"
                  :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData"
              :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload :action="uploadURL" :on-preview="handlePreview"
              :on-remove="handleRemove" list-type="picture"
              :headers="headersOBJ" :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addGoodForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="addBtn" @click="submitAdd">
            </el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt="" class="imgWidth" />
    </el-dialog>
  </div>
</template>

<script>
import { getCategories } from 'network/goodsCom/Categories'
import { getAttributesById } from 'network/goodsCom/Params'
import { addGood } from 'network/goodsCom/Goods'

import _ from 'lodash'
export default {
  name: 'GoodsAdd',
  components: {},
  data () {
    return {
      activeIndex: '0',
      // 
      addGoodForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_cat: [],
        goods_introduce: '',
        attrs: [],
        pics: []
      },
      addGoodRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      categoriesData: [],
      categoriesProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // url
      uploadURL: 'http://timemeetyou.com:8889/api/private/v1/upload',
      // 
      headersOBJ: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // URL
      previewPath: '',
      previewVisible: false
    }
  },
  computed: {
    cateId () {
      if (this.addGoodForm.goods_cat.length === 3) {
        return this.addGoodForm.goods_cat[2]
      }
      return null
    }
  },
  // data
  watch: {},
  methods: {
    // 
    submitAdd () {
      this.$refs.addGoodFormRef.validate(validate => {
        if (!validate) {
          this.$message.error('!')
          return false
        }
        // form
        const form = _.cloneDeep(this.addGoodForm)
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyTableData.forEach(item => {
          const newAttr = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          form.attrs.push(newAttr)
        })
        // 
        this.onlyTableData.forEach(item => {
          const newAttr = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          form.attrs.push(newAttr)
        })
        console.log(form)
        // 
        this.addGood(form)
      })
    },
    // 
    handleRemove (file) {
      // 
      const filePath = file.response.data.tmp_path
      // 
      const i = this.addGoodForm.pics.findIndex(x => x.pics === filePath)
      // splice
      this.addGoodForm.pics.splice(i, 1)
    },
    // 
    handlePreview (file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleSuccess (res) {
      // 
      const temp = { pic: res.data.tmp_path }
      // push
      this.addGoodForm.pics.push(temp)
    },
    // 
    tabClick () {
      console.log(this.activeIndex)
      if (this.activeIndex === '1') {
        this.getAttributesById(this.cateId, 'many')
      } else if (this.activeIndex === '2') {
        this.getAttributesById(this.cateId, 'only')
      }
    },
    // 
    beforeTabsLeave (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addGoodForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // 
    handleChange () {
      console.log(this.addGoodForm.goods_cat)
    },
    // 
    getCategories (type = '', pageNum = '', pageSize = '') {
      getCategories(type, pageNum, pageSize)
        .then(res => {
          if (res.data.meta.status !== 200) { return this.$message.error(res.data.meta.msg) }
          this.$message.success(res.data.meta.msg)
          this.categoriesData = res.data.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 
    getAttributesById (id, sel) {
      getAttributesById(id, sel)
        .then(res => {
          if (res.data.meta.status !== 200) { return this.$message.error(res.data.meta.msg) }
          // 
          if (res.data.data.length > 0 && sel === 'many') {
            res.data.data.forEach(item => {
              item.attr_vals =
                item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
            })
          }
          // 
          sel === 'only'
            ? (this.onlyTableData = res.data.data)
            : (this.manyTableData = res.data.data)
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 
    addGood (form) {
      addGood(form).then(res => {
        console.log(res)
        if (res.data.meta.status !== 201) { return this.$message.error(res.data.meta.msg) }
        this.$message.success(res.data.meta.msg)
        this.$router.push('/goods')
      }).catch(err => {
        console.log(err)
      })
    }
  },
  //  - this
  created () {
    this.getCategories()
  },
  //  - DOM
  mounted () { },
  beforeCreate () { }, //  - 
  beforeMount () { }, //  - 
  beforeUpdate () { }, //  - 
  updated () { }, //  - 
  beforeDestroy () { }, //  - 
  destroyed () { }, //  - 
  activated () { } // keep-alive
}
</script>

<style scoped>
.el-steps {
  margin: 10px auto;
}
.el-cascader {
  width: 50%;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.imgWidth {
  width: 100%;
}
.addBtn {
  margin-top: 10px;
}
</style>
